/* изучить препроцессоры */

* {
    font-family: Ubuntu;
    box-sizing: border-box;
}

:root {
    --accent: #373AF5;
    --secondary: #8083A4;
    --base-strong: #2C2D76;
    --critic: #E5596D;
    --base-weak: #FFFFFF;
}

body {
    padding-bottom: 60px;
    /* TODO size problem */
    min-height: 100vh;
    margin: 0;
    background-image: url(./resources/up-background.svg), url(./resources/down-background.svg);
    background-position: top right, bottom left;
    background-repeat: no-repeat, no-repeat;
    -moz-background-size: 100%; /* Firefox 3.6+ */
    -webkit-background-size: 100%; /* Safari 3.1+ и Chrome 4.0+ */
    -o-background-size: 100%; /* Opera 9.6+ */
    background-size: 100%;
    background-color: var(--accent);
}



.logo {
    margin: 65px auto 60px;
    width: 775px;
    height: 212px;

    background: url(./resources/logo.svg) no-repeat;

    -moz-background-size: 100%; /* Firefox 3.6+ */
    -webkit-background-size: 100%; /* Safari 3.1+ и Chrome 4.0+ */
    -o-background-size: 100%; /* Opera 9.6+ */
    background-size: 100%;
}

/* TODO больше ступений*/
@media (max-width: 850px) {
    .logo {
        width: 243px;
        height: 64px;
    }
    
}



.flex-row {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    gap: 34px;
    justify-content: center;
}
.flex-row__child {
    display: flex;
}



.card {
    background: var(--base-weak);
    box-shadow: 0 4px 4px rgba(4, 5, 73, 0.25), 14px 14px 20px rgba(5, 6, 114, 0.2);/*TODO color*/
    border-radius: 8px;
}

.card-main {
    padding: 50px 58px 90px;
    width: 660px;
}

@media (max-width: 680px) {
    .flex-row__child {
        width: 100%;
        margin-left: 10px;
        margin-right: 10px;
    }
    .card-main {
        width: 100%;
    }
}

.card__title {
    font-style: normal;
    font-weight: bold;
    font-size: 32px;
    line-height: 32px;
    color: var(--base-strong);
}
.card__title {
    margin-bottom: 32px;
}

.input-with-icon {
    width: 252px;
    height: 48px;
    border: 2px solid var(--secondary);
    border-radius: 8px;
}

.forecast-unselected-fields-stub__hint-text {
    font-style: normal;
    font-weight: bold;
    font-size: 16px;
    line-height: 24px;

    text-align: center;
    color: var(--secondary);
}

.forecast-unselected-fields-stub__img {
    display: flex;
    margin: 0 auto;
}